import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { useState, useEffect } from 'react';
import { LoadingSpinner } from '../common/LoadingSpinner';

const MainContent = styled.main`
  min-height: 100vh;
  background: ${props => props.theme.colors.primary};
  position: relative;
  overflow-x: hidden;
  padding: 0;
  width: 100%;
  
  @media (max-width: 768px) {
    padding-top: ${props => props.theme.spacing['4xl']};
  }
  
  &::before {
    content: '';
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: radial-gradient(circle at 70% 20%, rgba(0, 255, 231, 0.03) 0%, transparent 50%),
                radial-gradient(circle at 30% 80%, rgba(255, 0, 200, 0.03) 0%, transparent 50%);
    pointer-events: none;
  }
`;

const Section = styled(motion.section)`
  max-width: 1200px;
  margin: 0 auto;
  padding: ${props => props.theme.spacing.md};
  
  @media (max-width: 768px) {
    padding: ${props => props.theme.spacing.md};
  }

  &.loading {
    opacity: 0.5;
    pointer-events: none;
  }
`;

const SectionHeader = styled.div`
  margin-bottom: ${props => props.theme.spacing['2xl']};
  
  h1 {
    font-size: ${props => props.theme.typography.fontSize['5xl']};
    font-weight: ${props => props.theme.typography.fontWeight.bold};
    color: ${props => props.theme.colors.text.primary};
    margin-bottom: ${props => props.theme.spacing.md};
    
    span {
      color: ${props => props.theme.colors.text.accent};
    }
  }
  
  p {
    font-size: ${props => props.theme.typography.fontSize.xl};
    color: ${props => props.theme.colors.text.secondary};
    max-width: 600px;
  }
`;

// Animation variants
const pageVariants = {
  initial: { opacity: 0, x: 20 },
  enter: { 
    opacity: 1, 
    x: 0,
    transition: {
      duration: 0.5,
      ease: [0.4, 0, 0.2, 1]
    }
  },
  exit: { 
    opacity: 0, 
    x: -20,
    transition: {
      duration: 0.3,
      ease: [0.4, 0, 0.2, 1]
    }
  }
};

const Presentation = ({ sections, currentSection, sidebarWidth = 280 }) => {
  const [isLoading, setIsLoading] = useState(true);
  
  useEffect(() => {
    setIsLoading(true);
    const timer = setTimeout(() => setIsLoading(false), 500);
    return () => clearTimeout(timer);
  }, [currentSection]);
  
  return (
    <MainContent $sidebarWidth={sidebarWidth}>
      <AnimatePresence mode="wait">
        <Section
          key={sections[currentSection].id}
          className={isLoading ? 'loading' : ''}
          initial="initial"
          animate="enter"
          exit="exit"
          variants={pageVariants}
        >
          {isLoading ? (
            <LoadingSpinner />
          ) : (
            <>
              <SectionHeader>
                <h1>
                  <span>{sections[currentSection].icon}</span>
                  {sections[currentSection].title}
                </h1>
                <p>{sections[currentSection].description}</p>
              </SectionHeader>
              
              {/* Section-specific content */}
              {sections[currentSection].component}
            </>
          )}
        </Section>
      </AnimatePresence>
      <Masthead>
        <div className="author">Leon Chlon</div>
        <div className="company">Hassana Labs</div>
      </Masthead>
    </MainContent>
  );
};

// Add Masthead component for the footer
const Masthead = styled.footer`
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: rgba(18, 18, 24, 0.7);
  backdrop-filter: blur(8px);
  padding: ${props => props.theme.spacing.sm} 0;
  text-align: center;
  color: ${props => props.theme.colors.text.secondary};
  border-top: 1px solid ${props => props.theme.colors.glass.border};
  z-index: ${props => props.theme.zIndex.masthead || 10};
  
  .author {
    font-weight: ${props => props.theme.typography.fontWeight.semibold};
    font-size: ${props => props.theme.typography.fontSize.md};
    color: ${props => props.theme.colors.text.primary};
    margin-bottom: ${props => props.theme.spacing.xs};
  }
  
  .company {
    font-size: ${props => props.theme.typography.fontSize.sm};
    color: ${props => props.theme.colors.text.accent};
    opacity: 0.9;
  }
`;

export default Presentation;